<template>
    <div>
        <head-top webActive="mine"></head-top>
        <div class="bgGray" id="unIndex">
            <div class="container">
                <div class="ucBg">
                    <side-bar></side-bar>
                    <div class="ucPanel ucP_Box">
                        <div class="ucGreenTit">我的评价</div>
                        <el-table
                                :data="tableData"
                                style="width: 100%">
                            <el-table-column
                                    prop="score"
                                    label="评分"
                                    width="150"
                            >
                                <template slot-scope="scope">
                                    <el-rate
                                            v-model="scope.row.goods_rank "
                                            disabled>
                                    </el-rate>
                                        <p class="f12 color9">{{scope.row.create_time}}</p>
                                </template>
                            </el-table-column>
                            <el-table-column
                                    prop="content"
                                    label="评价内容"
                                    width="350"
                            >
                                <template slot-scope="scope">
                                    <div>{{scope.row.content}}</div>
                                    <div class="picContent">
                                        <el-image  v-for="(iitem,index) in scope.row.photos" :key="index"
                                                   class="picImg"
                                                   :src="iitem"
                                                   fit="cover"
                                                   :preview-src-list="scope.row.photos"></el-image>
                                    </div>
                                </template>
                            </el-table-column>
                            <el-table-column
                                    prop="goodname"
                                    label="商品信息"
                            >
                                <template slot-scope="scope">
                                    <div class="flex f-nowrap">
                                        <div class="ue-pic">
                                            <el-image class="ue-image" :src="scope.row.goodsPhotos" fit="cover"></el-image>
                                        </div>
                                        <div style="width:250px;">
                                            <p class="wto" :title="scope.row.goods_name">{{scope.row.goods_name}}</p>
                                            <div class="color9 f12">规格：{{scope.row.spec}}{{scope.row.spec_unit}}</div>
                                            <div class="f12">￥{{scope.row.price_cn}}/{{scope.row.supply_unit}}</div>
                                        </div>
                                    </div>
                                </template>
                            </el-table-column>
                            <el-table-column
                                    prop="time"
                                    label="操作"
                                    width="100"
                            >
                                <template slot-scope="scope">
                                    <el-link class="delComment" icon="el-icon-delete" @click="delComment(scope.row.comment_id)">删除</el-link>
                                </template>
                            </el-table-column>
                        </el-table>

                    </div>
                </div>
            </div>
        </div>
    </div>
</template>


<script>
    import sideBar from '../../../components/usercenter/sideBar';
    import {myComment,delComment} from '../../../service/api'
    import {mapState, mapMutations, mapActions} from 'vuex'
    import headTop from '../../../components/header/headTop';

    export default {
        name: 'myEvaluate',
        data(){
            return{
                tableData: []
            }
        },
        components:{
            sideBar,
            headTop
        },
        mounted(){
            this.initData();
        },
        computed:{
            ...mapState([
                'userInfo','login'
            ]),
        },
        methods:{
            ...mapActions([
                'getUserInfo'
            ]),
            async initData(){
                var res = await myComment();
                if(res){
                    if(res.status != 12007){
                        this.tableData = res;
                    }else{
                        this.$message({
                            message: res.msg,
                            type: 'warning'
                        });
                    }
                }

            },
            toDetail(goods_id){
                this.$router.push({path:"/good/detail",query:{goods_id:goods_id}});
            },
            async delComment(comment_id){
                this.$confirm('你确定要删除该评论吗?', '', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(async ()=> {
                    var res = await delComment(comment_id);
                    if(res.status==12000){
                        this.$message({
                            message:res.msg,
                            type:'success'
                        });
                        this.initData();
                    }else{
                        this.$message({
                            message:res.msg,
                            type:'warning'
                        });
                    }
                }).catch(() => {

                });
            }

        },
        created(){


        },
        watch:{
            userInfo: function (value) {
                if (value && value.user_id && !this.list) {
                    this.initData();
                }
            }

        }

    }

</script>

<style scoped>
    /*我的评价*/
    .picContent{
        display: flex;
        flex-wrap: nowrap;
        width:350px;
        margin-top:10px;
    }
    .picImg{
        width:50px;
        height:50px;
        display: inline-block;
        margin-right:10px;
    }
    .price{
        color:#d4282d;
    }
    .ue-image{
        width:70px;
        height: 70px;
        flex-shrink: 0;
        border:1px solid #e4e4e4;
        margin-right:15px;
    }


</style>



